* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.info-list,
.country-list  {
  list-style: none;
  margin: 0;
  padding: 0;
}



.wrap-title {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 10px;
}

.country-list > li {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 5px;
}

.country-list > li > p {
  margin: 0;
}

